<template>
  <Loading :active="isLoading" :opacity="0.8" :z-index="1000">
    <Loader></Loader>
  </Loading>
  <div class="product container">
    <div class="row d-flex justify-content-center">
      <div class="col-lg-4 col-md-6 my-4 mx-lg-2 text-center">
        <ProductSwiper :product="product" />
      </div>
      <div
        class="col-md-6 col-11 my-4 mx-lg-2 d-flex flex-column justify-content-between"
      >
        <div>
          <nav style="--bs-breadcrumb-divider: '>'" aria-label="breadcrumb">
            <ol class="breadcrumb">
              <li class="breadcrumb-item"><a href="#"></a></li>
              <li v-if="product.category === ''" class="breadcrumb-item">
                <a href="#/products"></a>
              </li>
              <li v-else class="breadcrumb-item">
                <a href="#/menu"></a>
              </li>
              <li class="breadcrumb-item active" aria-current="page">
                {{ product.title }}
              </li>
            </ol>
          </nav>
          <h2 class="mb-2 mb-lg-4">{{ product.title }}</h2>
          <span class="mb-0 product-content-title"></span>
          <p class="mb-2 mb-lg-4" v-html="product.description"></p>
          <span class="mb-0 product-content-title"></span>
          <p>{{ product.content }}</p>
        </div>
        <div v-if="product.category === ''">
          <p
            v-if="product.price === product.origin_price"
            class="text-dark fs-3"
          >
            $ {{ product.price }}
          </p>
          <p v-else class="text-danger fw-bold fs-3">
            $ {{ product.price }}
            <del class="text-muted fw-normal fs-5">
              $ {{ product.origin_price }}
            </del>
          </p>
          <div class="d-md-flex pb-2">
            <input
              class="col form-control me-2"
              type="number"
              min="1"
              v-model="qty"
            />
            <button
              class="btn btn-dark my-2 my-md-0"
              type="button"
              @click="addToCart(product)"
              :disabled="btnLoading"
            >
              
            </button>
          </div>
        </div>
        <div v-else class="text-dark fw-bold fs-4 pb-2">
          <span v-if="product.price_large">
            <span class="me-3"> ${{ product.price_large }}</span>
            
          </span>
          ${{ product.price }}
        </div>
      </div>
    </div>
  </div>
  <div class="container-fluid bg-secondary">
    <div
      class="container p-4 text-dark d-flex flex-column justify-content-center align-items-center"
    >
      <template v-if="product.category !== ''">
        <h5 class="fs-5 fw-bold ms-lg-2 mb-4 pb-1 border-bottom border-dark">
          
        </h5>
        <ul>
          <li class="mb-2">
            45 
          </li>
          <li class="mb-2">
             3
            
          </li>
          <li class="mb-2">
            
          </li>
          <li class="mb-2"> 50 </li>
          <li class="mb-2"></li>
          <li class="mb-2"></li>
          <li class="mb-2"></li>
        </ul>
      </template>
      <template v-else>
        <h5 class="fs-5 fw-bold ms-lg-2 mb-4 pb-1 border-bottom border-dark">
          
        </h5>
        <p class="col-lg-6">
          
        </p>
        <p class="col-lg-6">
          
        </p>
        <p class="col-lg-6">
          
        </p>
      </template>
    </div>
  </div>
  <div class="container p-4">
    <h4 class="fs-4 text-dark fw-bold ms-lg-2 mb-4"></h4>
    <AutoSwiper
      class="px-2"
      :products="categoryProducts"
      @get-products="getProduct"
    />
  </div>
</template>

<script>
import emitter from "@/utils/emitter";
import ProductSwiper from "@/components/ProductSwiper.vue";
import AutoSwiper from "@/components/AutoSwiper.vue";
import Loader from "@/components/LoadingComponents.vue";

export default {
  components: {
    ProductSwiper,
    AutoSwiper,
    Loader,
  },
  data() {
    return {
      id: "",
      qty: 1,
      product: {},
      isLoading: true,
      btnLoading: false,
      products: [],
      categoryProducts: [],
    };
  },
  methods: {
    getProduct() {
      this.id = this.$route.params.id;
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/product/${this.id}`;
      this.isLoading = true;
      this.$http
        .get(url)
        .then((res) => {
          this.product = res.data.product;
          this.getProducts();
          this.isLoading = false;
        })
        .catch((error) => {
          this.$httpMessageState(error.response, error.response.data.message);
        });
    },
    addToCart(product) {
      const data = {
        data: {
          product_id: product.id,
          qty: this.qty,
        },
      };
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/cart`;
      this.btnLoading = true;
      this.$http
        .post(url, data)
        .then((res) => {
          this.$httpMessageState(res, ` ${product.title} `);
          emitter.emit("get-cart");
          this.btnLoading = false;
        })
        .catch((error) => {
          this.$httpMessageState(error.response, error.response.data.message);
        });
    },
    getProducts() {
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/products/all`;
      this.$http
        .get(url)
        .then((res) => {
          this.products = res.data.products;
          this.getCategoryProducts();
        })
        .catch((error) => {
          this.$httpMessageState(error.response, error.response.data.message);
        });
    },
    getCategoryProducts() {
      const { category, id } = this.product;
      this.categoryProducts = this.products.filter(
        (item) => item.category === category && item.id != id
      );
    },
  },
  mounted() {
    this.getProduct();
  },
};
</script>
